.popup {
  visibility: hidden;
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  transition: all 0.1s linear;

  display: flex;
  justify-content: center;
}

@media (max-width: 550px) {
  .popup {
    padding: 0 19px 0;
  }
}
